<template>
  <div class="goods-carts">
    <van-overlay
      :show="loading"
      style="display: flex; align-items: center; justify-content: center"
    >
      <van-loading type="spinner" vertical color="#44d9c5" text-color="#44d9c5">
        数据加载中……
      </van-loading>
    </van-overlay>
    <div v-if="list.length === 0" class="nodata"></div>
    <div v-else>
      <van-sticky>
        <van-cell :title="'全部商品'">
          <span v-if="!editing" style="color: #333">编辑</span>
          <span v-if="editing" style="color: #333">完成</span>
        </van-cell>
      </van-sticky>
      <div class="carts-item">
        <van-cell-group v-for="item in list" :key="item.id" class="item-group">
          <van-cell :border="false" :title="item.shopName" icon="shop-o" />
          <van-cell style="padding: 0" :border="false">
            <van-checkbox
              v-for="goods in item.goods"
              :key="goods.id"
              :value="checkedIds.includes(goods.id)"
              checked-color="#44d9c5"
              class="item-box"
              :label-disabled="true"
              @click="checkItem(goods)"
            >
              <van-card
                :price="goods.price"
                :title="goods.goodsName"
                :thumb="goods.goodsImg"
              >
                <template #num>
                  <van-stepper
                    v-model="goods.num"
                    :max="goods.maxNum"
                    integer
                    style="margin-top: -2vw"
                    @change="handleChangeNum"
                    @click.native.stop="() => {}"
                  />
                </template>
              </van-card>
            </van-checkbox>
          </van-cell>
        </van-cell-group>
      </div>
      <div class="carts-sub">
        <van-submit-bar
          :price="subPrice"
          button-text="结 算"
          button-color="#44d9c5"
          @submit="onSubmit"
        >
          <van-checkbox
            v-model="checked"
            checked-color="#44d9c5"
            @change="checkAll"
          >
            全选
          </van-checkbox>
        </van-submit-bar>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import {
  CellGroup,
  Cell,
  Checkbox,
  Card,
  Sticky,
  Stepper,
  Overlay,
  Loading,
  SubmitBar,
} from "vant";

export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Checkbox.name]: Checkbox,
    [Card.name]: Card,
    [Sticky.name]: Sticky,
    [Stepper.name]: Stepper,
    [Overlay.name]: Overlay,
    [Loading.name]: Loading,
    [SubmitBar.name]: SubmitBar,
  },
  data() {
    return {
      editing: false,
      loading: false,
      checked: false,
      subPrice: 0,
      list: [],
      canCheckNum: 0,
      checkedIds: [],
    };
  },
  created() {
    this.getList();
  },
  methods: {
    handleChangeNum(val) {
      console.log(1111111, val);
    },
    getList() {
      this.loading = true;
      setTimeout(() => {
        this.list = [
          {
            id: 1,
            shopName: "江苏旅游职业学院xx奶茶店",
            goods: [
              {
                id: "EYE_1001",
                goodsName: "芋泥波波奶茶",
                num: 1,
                maxNum: 99,
                price: 20,
                goodsImg:
                  "https://tse1-mm.cn.bing.net/th/id/R-C.e3b7b19911fb1589bd9f8b4b472a7fd0?rik=A7bbot%2fJn7tq3w&riu=http%3a%2f%2fwww.wahahayp.com%2fuploadfiles%2f2020%2f12%2f20201218120246711.png&ehk=d6lEnDw0ApqrfXSXngtasOcb4U8J0hoVoA%2bTgg1SGDQ%3d&risl=&pid=ImgRaw&r=0",
              },
              {
                id: "EYE_1002",
                goodsName: "百香果双响炮",
                num: 1,
                maxNum: 99,
                price: 25,
                goodsImg:
                  "https://tse1-mm.cn.bing.net/th/id/R-C.e3b7b19911fb1589bd9f8b4b472a7fd0?rik=A7bbot%2fJn7tq3w&riu=http%3a%2f%2fwww.wahahayp.com%2fuploadfiles%2f2020%2f12%2f20201218120246711.png&ehk=d6lEnDw0ApqrfXSXngtasOcb4U8J0hoVoA%2bTgg1SGDQ%3d&risl=&pid=ImgRaw&r=0",
              },
            ],
          },
          {
            id: 2,
            shopName: "江苏旅游职业学院第一食堂一层扬州炒饭",
            goods: [
              {
                id: "EYE_1003",
                goodsName: "虾仁炒饭",
                num: 2,
                maxNum: 99,
                price: 19,
                goodsImg:
                  "https://tse1-mm.cn.bing.net/th/id/R-C.d58a47ccb1e9cec9cd0cc22ed4a09414?rik=AE1jtv6K%2bz6EJw&riu=http%3a%2f%2fi2.chuimg.com%2f6f9703668a1811e6b87c0242ac110003_2048w_1828h.jpg%3fimageView2%2f2%2fw%2f660%2finterlace%2f1%2fq%2f90&ehk=WClHlBJ2%2bUIXsSbnXKoc9VmVnvDrQicjlfHtVQclqx8%3d&risl=&pid=ImgRaw&r=0",
              },
            ],
          },
        ];

        _.map(this.list, (item) => {
          _.map(_.get(item, "goods", []), () => {
            this.canCheckNum++;
          });
        });

        this.loading = false;
      }, 3000);
    },
    checkItem(goods) {
      if (this.checkedIds.includes(goods.id)) {
        this.checkedIds = _.filter(this.checkedIds, (id) => id !== goods.id);
        this.subPrice -= goods.price * 100 * goods.num;
        this.checked = false;
      } else {
        this.checkedIds.push(goods.id);
        this.subPrice += goods.price * 100 * goods.num;
      }
      if (this.checkedIds.length === this.canCheckNum) {
        this.checked = true;
      }
    },
    checkAll(val) {
      if (val) {
        if (this.checkedIds.length !== this.canCheckNum) {
          _.map(this.list, (item) => {
            _.map(_.get(item, "goods", []), (goods) => {
              this.checkedIds.push(goods.id);
              this.subPrice += goods.price * 100 * goods.num;
            });
          });
        }
      } else {
        if (this.checkedIds.length === this.canCheckNum) {
          this.checkedIds = [];
          this.subPrice = 0;
        }
      }
    },
    onSubmit() {},
  },
};
</script>

<style lang="less">
.goods-carts {
  .van-submit-bar__button--danger {
    background: #44d9c5;
    border: none;
  }

  .carts-item {
    .item-group {
      margin: 3vw 0;

      .item-box {
        padding: 2vw 4vw;

        .van-checkbox__label {
          width: 100%;
        }
      }
    }
  }
}
</style>
