<template>
  <div>
    <van-search placeholder="点击前往搜索" shape="round" @click="tosearch" />

    <!-- <van-tree-select
      height="85vh"
      :items="items"
      :main-active-index.sync="active"
    >
      <template #content>
        <div v-if="active === 0">
          <van-grid :column-num="3" :border="false">
            <van-grid-item v-for="item in classifyContent" :key="item.id">
              <van-image :src="item.pic" class="contentImg" lazy-load />
              <div style="font-family: Microsoft YaHei">{{ item.name }}</div>
            </van-grid-item>
          </van-grid>
        </div>
      </template>
    </van-tree-select> -->

    <!-- 左边 -->
    <van-sidebar v-model="activeKey" class="left" @change="sidebarChange">
      <van-sidebar-item
        v-for="item in items"
        :key="item.id"
        :title="item.text"
        style="text-align: center"
      />
    </van-sidebar>
    <!-- 右边 -->
    <div class="right">
      <div class="topContent">
        <van-image :src="top.pic" width="250px" />
      </div>
      <van-grid :column-num="num" :border="false">
        <van-grid-item v-for="item in classifyContent" :key="item.id">
          <van-image :src="item.pic" class="contentImg" />
          <div style="font-family: Microsoft YaHei">{{ item.name }}</div>
        </van-grid-item>
      </van-grid>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0,
      items: [],
      classifyContent: [],
      top: {},
      num: 3,
    };
  },
  methods: {
    async getinfo() {
      const { data: res } = await this.$http.get("/sort");
      this.items = res.items;
      // this.classifyContent = res.classifyContent;
      // this.top = res.top;
    },
    async firstRequest() {
      const { data: res } = await this.$http.post("/ases", {
        id: 1000,
      });
      this.classifyContent = res.Inquire.list;
      this.num = res.Inquire.num;
      this.top = res.Inquire.top;
    },
    async sidebarChange(e) {
      // console.log(e);
      // console.log(this.items[e].id);
      const { data: res } = await this.$http.post("/ases", {
        id: this.items[e].id,
      });
      this.classifyContent = res.Inquire.list;
      this.num = res.Inquire.num;
      this.top = res.Inquire.top;
    },
    tosearch() {
      this.$router.push("/search");
    },
  },
  created() {
    this.getinfo();
    this.firstRequest();
  },
};
</script>
<style lang="less" scoped>
.left {
  float: left;
  overflow: scroll;
  height: 84vh;
}
.right {
  margin-left: 80px;
  overflow: scroll;
  height: 84vh;
}
.topContent {
  text-align: center;
}
</style>